import { reactive, watch } from 'vue'

interface Dark {
    isDark: boolean
}
interface Color {
    color: string
}
interface CurrentTheme {
    current: Dark & Color
}
interface Colors {
    color: string[]
}

type Theme = Colors & CurrentTheme

/**
 * 
 */
const THEME = reactive<Theme>({
    /**
     * color
     */
    color: ['default', 'green', 'red', 'blue'],

    /**
     * current
     */
    current: JSON.parse(localStorage.getItem('theme-current') as string) || {
        isDark: false,
        /**
         * 
         */
        color: 'default'
    }
})

/**
 * , THEME.current.isDarktruedocument.bodydark
 */
watch(
    () => THEME.current.isDark,
    () => {
        if (THEME.current.isDark) {
            document.body.classList.add('dark')
        } else {
            document.body.classList.remove('dark')
        }
    },
    { immediate: true }
)

/**
 * 
 */
watch(
    () => THEME.current.color,
    () => {
        THEME.color.forEach((element) => {
            document.body.classList.remove(element)
        })
        document.body.classList.add(THEME.current.color)
    },
    { immediate: true }
)

/**
 * THEMETHEMElocalStorage
 */
watch(
    () => THEME.current,
    () => localStorage.setItem('theme-current', JSON.stringify(THEME.current))
)

export default function useDark() {
    /**
     * THEME
     * @returns const Object THEME
     */
    const get = (): Theme => THEME
    const getCurrent = (): Dark & Color => THEME.current
    const getThemeColor = (): string[] => THEME.color

    /**
     * THEME.current
     * @param {Object} current THEME.current
     */
    const set = (current: Dark | Color) => {
        THEME.current = {
            ...THEME.current,
            ...current
        }
    }

    return {
        get,
        getCurrent,
        getThemeColor,
        set
    }
}

export type { Theme, Colors, CurrentTheme, Dark, Color }
